"use client";

import React from "react";
import Link from "next/link";
import Image from "next/image";
import { usePathname } from "next/navigation";
import { UserButton } from "@clerk/nextjs";
import {
  Search,
  Bell,
  Sun,
  ChevronRight,
  Command,
  LayoutDashboard,
  ChevronsUpDown,
} from "lucide-react";

export function Navbar() {
  const pathname = usePathname();

  // Helper to generate dynamic breadcrumb based on path
  const getBreadcrumb = () => {
    if (pathname === "/dashboard") return "Overview";

    const segments = pathname.split("/").filter(Boolean);
    const currentPage = segments.length > 0 
      ? segments[segments.length - 1].replace(/-/g, " ").replace(/\b\w/g, c => c.toUpperCase()) 
      : "Overview";
    return currentPage;
  };

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 h-16 bg-gradient-to-r from-[#0B1221] to-[#0F1528] border-b border-white/5 flex items-center justify-between px-4 md:px-6 transition-all duration-300 shadow-sm">
      
      {/* --- LEFT SECTION: Brand, Switcher, Breadcrumb --- */}
      <div className="flex items-center gap-4 md:gap-6">
        {/* Logo & Brand */}
        <Link href="/dashboard" className="flex items-center gap-3 group">
          {/* Logo Image - Official sl.png with no borders/radius */}
          <Image 
            src="/sl.png" 
            alt="SoloBotAgency Logo" 
            width={34} 
            height={34}
            className="transition-transform duration-200 group-hover:scale-105"
          />
          <span className="hidden md:block font-bold text-sm text-slate-100 tracking-tight group-hover:text-white transition-colors">
            SoloBotAgency
          </span>
        </Link>

        {/* Divider */}
        <div className="hidden md:block h-4 w-px bg-white/10 rotate-12"></div>

        {/* Workspace Switcher Pill */}
        <button className="hidden md:flex items-center gap-2 px-3 py-1 rounded-full border border-white/10 bg-white/[0.02] hover:bg-white/5 hover:border-white/20 transition-all group">
          <div className="w-4 h-4 rounded-full bg-indigo-500/20 flex items-center justify-center text-[10px] font-bold text-indigo-400">
            A
          </div>
          <span className="text-xs font-medium text-slate-300 group-hover:text-white">Agency Internal</span>
          <ChevronsUpDown className="w-3 h-3 text-slate-500 group-hover:text-slate-400" />
        </button>

        {/* Breadcrumb (Mobile/Tablet Hidden) */}
        <div className="hidden lg:flex items-center gap-2 text-sm">
          <ChevronRight className="w-4 h-4 text-slate-600" />
          <span className="text-slate-500 font-medium">Dashboard</span>
          <ChevronRight className="w-4 h-4 text-slate-600" />
          <span className="text-slate-200 font-medium">{getBreadcrumb()}</span>
        </div>
      </div>

      {/* --- CENTER SECTION: Linear-style Command Bar --- */}
      <div className="hidden md:flex absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2">
        <button 
          className="group relative flex items-center gap-3 w-[340px] lg:w-[400px] h-9 px-4 rounded-full bg-white/[0.03] hover:bg-white/[0.06] backdrop-blur-lg border border-white/10 shadow-sm transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-indigo-500/30"
          onClick={() => alert("Global Command Palette (Cmd+K)")} 
        >
          <Search className="w-3.5 h-3.5 text-slate-500 group-hover:text-slate-300 transition-colors" />
          <span className="text-xs font-medium text-slate-500 group-hover:text-slate-400 text-left flex-1 truncate">
            Search commands...
          </span>
          <div className="flex items-center gap-1 px-1.5 py-0.5 rounded border border-white/10 bg-white/5 text-[10px] font-medium text-slate-500 group-hover:text-slate-300 group-hover:border-white/20 transition-all">
            <Command className="w-2.5 h-2.5" />
            <span>K</span>
          </div>
          
          <div className="absolute inset-0 rounded-full bg-indigo-500/5 opacity-0 group-hover:opacity-100 transition-opacity blur-xl -z-10"></div>
        </button>
      </div>

      {/* --- RIGHT SECTION: Actions & User --- */}
      <div className="flex items-center gap-3 md:gap-5">
        
        {/* Dashboard/Action Pill */}
        <Link 
          href="/dashboard"
          className="hidden md:flex items-center gap-2 px-4 py-1.5 rounded-full border border-white/10 bg-transparent text-xs font-medium text-slate-400 hover:text-indigo-300 hover:border-indigo-500/30 hover:bg-indigo-500/5 transition-all duration-300 group"
        >
          <LayoutDashboard className="w-3.5 h-3.5 group-hover:text-indigo-400 transition-colors" />
          <span>Overview</span>
        </Link>

        {/* Icons Group */}
        <div className="flex items-center gap-1 md:gap-2">
            <button className="p-2 rounded-full text-slate-400 hover:text-white hover:bg-white/5 transition-all duration-200 relative group">
                <Sun className="w-[18px] h-[18px]" />
                <span className="sr-only">Toggle Theme</span>
            </button>

            <button className="p-2 rounded-full text-slate-400 hover:text-white hover:bg-white/5 transition-all duration-200 relative group">
                <Bell className="w-[18px] h-[18px]" />
                <span className="absolute top-2 right-2 w-2 h-2 bg-rose-500 rounded-full border-2 border-[#0B1221] shadow-sm animate-pulse"></span>
            </button>
        </div>

        <div className="h-6 w-px bg-white/10"></div>

        {/* Clerk User Avatar with Online Indicator */}
        <div className="relative flex items-center justify-center p-0.5 rounded-full border border-white/10 hover:border-indigo-500/50 transition-colors cursor-pointer bg-white/[0.02]">
            <div className="relative">
                <UserButton 
                    afterSignOutUrl="/"
                    appearance={{
                        elements: {
                            avatarBox: "w-8 h-8 rounded-full",
                            userButtonPopoverCard: "bg-[#0B1221] border border-white/10 shadow-2xl",
                            userButtonPopoverFooter: "hidden"
                        }
                    }}
                />
                <div className="absolute bottom-0 right-0 w-2.5 h-2.5 bg-emerald-500 rounded-full border-[2px] border-[#0B1221] z-10 pointer-events-none shadow-sm"></div>
            </div>
        </div>

      </div>
    </nav>
  );
}